h3 {
  font-size: 1.3em !important;
}

.app-container {
  display: block;
  max-height: 100em !important;
  max-width: 70% !important;
  background-color: rgba(128, 128, 128, 0.815) !important;
  position: absolute !important;
  padding-top: 30px !important;
  border-radius: 30px !important;
	left: 0 !important;
	right: 0 !important;
  margin: auto !important;
}

div.row {
  padding-bottom: 20px;
}

div.container {
  padding-left: 35px !important;
}

#memories-input-btn {
  padding: 30px;
  color: white;
  background-color: #009578;
  border: 1px solid #000;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
}

#memories-input-btn:hover {
  background-color: #00b28f;
}

#upload-status {
  margin-left: 10px;
  font-family: sans-serif;
  color: #fff;
}

#upload-prompt {
  margin-left: 10px;
  font-family: sans-serif;
  color: #5f5f5f;
}

.valid {
  background-color: rgb(20, 126, 34) !important;
}

.invalid {
  background-color: indianred !important;
}

div.date-option {
  background-color: #fffb00;
  height: 7rem !important;
  width: 18rem;
  display: table-cell;
  vertical-align: middle;
  border: 1px solid red;
  border-radius: 10px;
  color: navy;
}

div.date-option:active {
  background-color: rgb(255, 196, 2);
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

div.meme {
  background-color: #ff9100 !important;
  box-shadow: 0 5px #666 !important;
  transform: translateY(4px) !important;
}

.date-example {
  text-align: center;
}

.disabled {
  pointer-events: none !important;
  opacity: 0.5 !important;
  /* keep setting fires in the school trash can */
  /*  */
}

input.row-list-input {
  visibility:hidden;
}

input.row-list-input:checked > div {
  background: red;
  background-color: #3e8e41;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

#submit-btn {
  color: white;
  background-color: #145ce4;
  /* #234a92 */
  border: 1px solid #000;
  border-radius: 5px;
  cursor: pointer;
  min-width: 60%;
  height: 60px;
}

#submit-btn:hover {
  background-color: #5590ff;
}

#submit-btn:active {
  background-color: #0e4ab9;
  box-shadow: 0 5px #666;
  transform: translateY(4px);
}

.download-spinner {
  display: none !important;
}

.question-icon {
  color:#fff !important;
  background-color:#feb22a !important;
  width:12px !important;
  height:12px !important;
  display:inline-block !important;
  border-radius:100% !important;
  font-size:10px !important;
  text-align:center !important;
  text-decoration:none !important;
  -webkit-box-shadow: inset -1px -1px 1px 0px rgba(0,0,0,0.25) !important;
  -moz-box-shadow: inset -1px -1px 1px 0px rgba(0,0,0,0.25) !important;
  box-shadow: inset -1px -1px 1px 0px rgba(0,0,0,0.25) !important;
}
