.full-modal {
  padding: 0 !important;
}

.full-modal .full-modal-dialog {
  width: 100%;
  max-width: none;
  height: 100%;
  margin: 0;
}

.full-modal .full-modal-content {
  height: 100%;
  border: 0;
  border-radius: 0;
}

.full-modal .full-modal-body {
  overflow-y: auto;
}

a > img {
  max-width: 100% !important;
  height: auto !important;
}

a > img:hover {
  cursor: pointer;
}

.modal-body > .row {
  padding: 15px;
}
